.compliance-container-modal{
    position: "fixed";
    transform: "translate(116px, 96px)";
    // zIndex: 9999;
    display: flex;
    // align-items: center;
    padding-top: 55px;
    justify-content: center;
  
    // padding: 45px;
    .modal-title{
        font-family: Nunito;
        color: #3A3A3A;
        font-size: 17px;
        font-style: normal;
        font-weight: 700;
        line-height: 26.4px;
        padding-top: 20px;
  }
  
  .centered-content {
    background-color: white;
    // padding: 20px;
    border-radius: 8px;
    height:90%;
    overflow-y: auto;
    // width: 60%;
    min-width: 80%;
    padding: 40px;
    position: relative;
  
  }
  
  .customer-porfile-container-modal{
    background: white;
    // padding: 15px 40px 24px 40px;
    border-radius: 12px;
    background: #FFF;
    // box-shadow: 0px 1px 15px 0px rgba(0, 0, 0, 0.04);
    display:flex;


  }}

  .compliance-container-modal:focus{
    outline:none;
  }

  .compliance-user-info-container{
    .transcation-usertype{
        color: #6B7280;
        font-family: Nunito;
        font-size: 12px;
        font-style: normal;
        font-weight: 600;
        line-height: 23.912px; /* 199.264% */
    }
    img{
        margin-top: 10px;
    }

    .transaction-user-detail-container{
        margin-top: 20px;
    }

    .transaction-user-detail{
        color: #6B7280;
        font-family: Nunito;
        font-size: 12px;
        font-style: normal;
        font-weight: 600;
        line-height: 23.912px; /* 199.264% */
        padding-bottom: 9px;
        padding-top: 9px;
        border-bottom: 1px solid #E6E6E6;


        span{
            padding-right: 19px;
        }
    }


}

.compliance-modal-details-container{
    gap:10px;
    margin-top: 25px;

}

.compliance-user-details {
    border-radius: 9px;
    border: 1px solid #CDCDCD;
    width: 100%;
    padding: 8px 17px;
}

.compliance-detail-information{
    width:100%;
    padding: 8px 17px;

}

.transaction-modal-transaction-status{
    margin-top: 25px;

    span{
        color: #00365A;
        font-family: Nunito;
        font-size: 15px;
        font-style: normal;
        font-weight: 500;
        line-height: 30px; /* 200% */
        margin-right: 10px;
    }
}

.transaction-modal-header{
    display:flex;
    gap: 25px;
    color: #6B7280;
    text-align: center;
    font-family: Nunito;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: 23.912px;
    align-items: center;
}

.compliance-modal-table-container{
    height: 500px;
    overflow-y: auto;
}

.previous-transaction-container{

    .last-selected-user{
        margin-top: 25px;
        // position:absolute;
        // bottom: 21px;
        width: 100%;
        background-color: #F2F2F2;
        padding: 17px 29px;
        border-radius: 12px;
    }

    .previously-selected-user{
        border-radius: 12px;
        background: #FFF;
        box-shadow: 0px 1px 15px 0px rgba(0, 0, 0, 0.04);
        padding: 10px;
        // padding-top:8px; 
        // padding-bottom: 8px;
        align-items: center;
        // padding-right: 80px;
        color: #3A3A3A;
        font-family: Nunito;
        font-size: 14px;
        font-style: normal;
        font-weight: 600;
        line-height: 26.4px; /* 155.294% */
        align-items: center;
        cursor: pointer;

        img{
            padding-right: 10px;
        }
        // width: 200px;
    }

    .previous-user-container{
        gap: 22px;
    }
}