.transaction-modal-tab-container{
    margin-top: 35px;

    .transaction-modal-header{
        display:flex;
        gap: 25px;
        color: #6B7280;
        text-align: center;
        font-family: Nunito;
        font-size: 12px;
        font-style: normal;
        font-weight: 600;
        line-height: 23.912px;
        align-items: center;
    }

    .transaction-modal-deposit-type{
        border-radius: 19.926px;
        background: #5523D2;
        padding: 5px 35px;
        color: #FFF;
        text-align: center;
        font-family: Nunito;
        font-size: 12px;
        font-style: normal;
        font-weight: 700;
        line-height: 23.912px; /* 199.264% */
    }

    .transaction-modal-details-container{
        gap:10px;
    }

    .transaction-user-details{
        // min-height: 50%;
        border-radius: 9px;
        border: 1px solid #CDCDCD;
        width: 100%;
        padding: 8px 17px;
    }

    .transaction-detail-information{
        padding: 8px 17px;

    }

    .transaction-modal-details-container{
        margin-top: 25px;
    }

    .transaction-modal-transaction-details{
        padding: 20px 35px;
        .transaction-user-detail{
            color: #6B7280;
            font-family: Nunito;
            font-size: 12px;
            font-style: normal;
            font-weight: 600;
            line-height: 23.912px; /* 199.264% */
            padding-bottom: 9px;
            padding-top: 9px;
            border-bottom: 1px solid #E6E6E6;
    
    
            span{
                padding-right: 19px;
            }
        }

        .transaction-status-text{
            color: #6B7280;
            font-family: Nunito;
            font-size: 14px;
            font-style: normal;
            font-weight: 600;
            line-height: 23.912px; /* 170.798% */
        }

        .transaction-modal-transaction-status{
            margin-top: 25px;

            span{
                color: #00365A;
                font-family: Nunito;
                font-size: 15px;
                font-style: normal;
                font-weight: 500;
                line-height: 30px; /* 200% */
                margin-right: 10px;
            }
        }
    }

    .transaction-id-csv{
        gap: 15px;
        align-items: center;
    }

    
}

