.position-pill{
    width: 154px;
    height: 35px;
    text-align: center;
    border-radius: 21px;
    // background:green;
    color: #FFF;
    font-family: Nunito;
    font-size: 15px;
    font-style: normal;
    font-weight: 700;
    line-height: 33.6px; /* 224% */

}

.currency-modal-select{
    border-radius: 4px;
    border: 1px solid #B1BEC5;
    color: #6B7280;
    font-family: Inter;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px; /* 200% */
    padding: 10px 25px 10px 10px;

}

.currency-modal-select:focus {
    outline: none;
}