.liquidity-modal-container{
    position: "fixed";
    transform: "translate(116px, 96px)";
    // zIndex: 9999;
    display: flex;
    // align-items: center;
    padding-top: 75px;
    justify-content: center;

    .modal-title{
        font-family: Nunito;
        color: #3A3A3A;
        font-size: 17px;
        font-style: normal;
        font-weight: 700;
        line-height: 26.4px;
        padding-top: 20px;
  }

  .centered-content {
    background-color: white;
    // padding: 20px;
    border-radius: 8px;
    height:80%;
    // width: 60%;
    min-width: 50%;
  }

  .customer-porfile-container-modal{
    background: white;
    // padding: 15px 40px 24px 40px;
    border-radius: 12px;
    background: #FFF;
    // box-shadow: 0px 1px 15px 0px rgba(0, 0, 0, 0.04);
    display:flex;
  }

  .add-new-liquidity-container{
    padding: 24px;
  }

  .liquidity-modal-header{
    margin-top: 50px;
    color: #00365A;
    text-align: center;
    font-family: Nunito;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 26.4px; /* 110% */
  }

  .form-liquidity-container{
    margin-top: 53px;
    
  }

  .liquidity-label{
    color: #6B7280;
    text-align: right;
    /* Body/Regular */
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px; /* 187.5% */
  }

  .liquidity-input select{
    border-radius: 4px;
    border: 1px solid #D6E1E6;
    width: 100%;
    color: #6B7280;
    font-family: Inter;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px; /* 200% */
    padding: 10px 10px;
  }
  .banking-account-row{
    margin-top: 17px;
  }

  .liquidity-input select:focus{
    outline: none;
  }

  .liquidity-modal-buttons{
    gap: 7px;
    margin-top: 46px;

    button{
        border: none;
        cursor:pointer;
    }

    button:hover{
        opacity: 0.8;
    }
  }

  .liquidity-link-button{
    border-radius: 5px;
    background: #3BC97B;
    color: #FFF;
    text-align: center;
    font-family: Nunito;
    font-size: 15px;
    font-style: normal;
    font-weight: 700;
    line-height: 33.6px; /* 224% */
    padding: 3px 20px;
  }

  .liquidity-cancel-button{
    border-radius: 5px;
    background: #CCC;
    color: #FFF;
    text-align: center;
    font-family: Nunito;
    font-size: 15px;
    font-style: normal;
    font-weight: 700;
    line-height: 33.6px; /* 224% */
    padding: 3px 15px;
  }

  .liquidity-modal-note{
    text-align: center;
    margin-top: 25px;
    color: #3A3A3A;
    text-align: center;
    /* Body/Regular */
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px; /* 187.5% */
  }
}
  