
.treasurying-details-button{
    
    color: #FFF;
    // text-align: center;
    font-family: Nunito;
    font-size: 15px;
    font-style: normal;
    font-weight: 700;
    line-height: 33.6px; /* 224% */
    border-radius: 5px;
    background: #008ECC;
    padding:5px 30px;
    // width:83px;
    // height: 37px;
    display:flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.treasurying-details-button:hover{
    opacity:0.8;
}

.treasurying-details-container{
    // // background-color: yellow;
    // display:flex;
    // justify-content: flex-end;
    padding: 0;
}

.treasurying-filter-container{
    display:flex;
    justify-content: center;
    gap: 40px;
    margin-top: 58px;
    margin-bottom: 100px;
    align-items: center;
}

.treasury-filtering-options {
    color: #6B7280;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
}

.treasury-filtering-options label {
    padding-right: 30px;
}
.filter-search{
    border-radius: 33px;
    background: #3BC97B;
    border: none;
    display: flex;
    color: white;
    align-items: center;
    padding: 10px 25px;
    cursor: pointer;
}

.filter-search:hover {
    opacity:0.8
}

.treasury-filtering-options input {
    color: #6B7280;
    font-family: Inter;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
    min-width: 200px;
    min-height: 49px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 4px;
    border: 1px solid #D6E1E6;
}

@media (max-width: 1500px) {
    .treasury-charts-container .col-xl-4 {
      flex: 0 0 100%; 
      max-width: 100%;
    }
  }

.treasury-summary-container{
    .treasury-chart-title{
        color: #3A3A3A;
        font-family: Nunito;
        font-size: 20px;
        font-style: normal;
        font-weight: 700;
        line-height: 33.6px; /* 168% */
    }



    .treasury-chart-box{
        margin-top: 14px;
    }

    .chart-header{
        color: #4C5566;
        font-family: Nunito;
        font-size: 14px;
        font-style: normal;
        font-weight: 600;
        line-height: 33.6px; /* 240% */
        text-align: center;
    }

    .liquidity-chart{
        margin:auto;
        height: 115px;
        width: 115px;
        background-color: #F3F3F3;
        display:flex;
        justify-content: center;
        align-items: center;
        color: #000;
        text-align: center;
        font-family: Nunito;
        font-size: 35px;
        font-style: normal;
        font-weight: 700;
        line-height: 33.6px; /* 96% */
        border-radius: 50%;
    }

    .liquidity-chart30{
        margin:auto;
        height: 115px;
        width: 115px;
        background-color: #eff6d2;
        display:flex;
        justify-content: center;
        align-items: center;
        color: #000;
        text-align: center;
        font-family: Nunito;
        font-size: 35px;
        font-style: normal;
        font-weight: 700;
        line-height: 33.6px; /* 96% */
        border-radius: 50%;
    }

    .grey-border-right{
        border-right: 1px solid #E6E6E6;
    }

    .currency-box{
        width: 146px;
        height: 35px;
        border-radius: 21px;
        background: #F2F2F2;
        display:flex;
        justify-content: center;
        align-items: center;
        color: #3A3A3A;
        text-align: center;
        font-family: Nunito;
        font-size: 15px;
        font-style: normal;
        font-weight: 500;
        line-height: 33.6px; /* 224% */
    }

    .treasury-currency-container{
        margin-top: 50px;
        gap: 23px;
    }

    .currency-history-table-container{
        margin-top: 40px;
    }
}
