.post-entry-button{
    border-radius: 33px;
    background: #145388;
    padding-top: 8px;
    padding-bottom: 8px;
    color: #FFF;
    font-family: Nunito;
    font-size: 15px;
    font-style: normal;
    font-weight: 600;
    line-height: 26.4px;
    display: flex;
    justify-content: space-evenly;
    width: 173px;
    cursor: pointer;
}

.post-entry-button:hover{
    opacity: 0.8;
}

.accounting-container{
    .transaction-selector{
        padding: 8px 8px;
    }

    .chart-container{
        display:flex;
        gap: 13px;
        margin-top:15px;
        

        .compliance-charts {
            flex: 1;
            border-radius: 12px;
            background: #FFF;
            box-shadow: 0px 1px 15px 0px rgba(0, 0, 0, 0.04);
                }

    }
    .transaction-history{
        margin-top: 12px;
        padding: 32px 85px;
        border-radius: 12px;
        background: #FFF;
        box-shadow: 0px 1px 15px 0px rgba(0, 0, 0, 0.04);

        .transaction-history-title{
            color: #3A3A3A;
            font-family: Nunito;
            font-size: 23px;
            font-style: normal;
            font-weight: 800;
            line-height: 33.6px; /* 146.087% */
        }
    }

    .transaction-option-container{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .transaction-filter-container{
        display: flex;
        gap: 23px;
        
    }

    .transaction-sent-received{
        label {
            color: #6B7280;
            font-family: Nunito;
            font-size: 15px;
            font-style: normal;
            font-weight: 800;
            line-height: 33.6px; /* 224% */


        }
        

    }
    .transaction-radios{
        margin-left: 12px;
        margin-right: 12px;
    }

    .transaction-selector{
        display:flex;
        align-items: center;
        border-radius: 5px;
        border: 1px solid #D7D7D7;



        select {
            border: none;
            width: 150px;
            color: #6B7280;
            font-family: Nunito;
            font-size: 14px;
            font-style: normal;
            font-weight: 800;
            line-height: 33.6px; /* 240% */
        }
    }

    .selector-titles{
        color: #6B7280;
        font-family: Nunito;
        font-size: 14px;
        font-style: normal;
        font-weight: 800;
        line-height: 33.6px;
    }

    .excel-icon{
        height: 25px;
    }
}