.currency-modal-container{
    position: "fixed";
    transform: "translate(116px, 96px)";
    // zIndex: 9999;
    display: flex;
    // align-items: center;
    padding-top: 200px;
    justify-content: center;

    .modal-title{
        font-family: Nunito;
        color: #3A3A3A;
        font-size: 17px;
        font-style: normal;
        font-weight: 700;
        line-height: 26.4px;
        padding-top: 20px;
  }

  .centered-content {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    height:80%;
    // width: 60%;
    min-width: 85%;
  }

  .customer-porfile-container-modal{
    background: white;
    // padding: 15px 40px 24px 40px;
    border-radius: 12px;
    background: #FFF;
    // box-shadow: 0px 1px 15px 0px rgba(0, 0, 0, 0.04);
    display:flex;
  }

  img{
    cursor:pointer;
  }
  img:hover{
    opacity: 0.8
  }
}

.currency-modal-container:focus{
    outline:none;
}

.currency-modal-header{
    color: #3A3A3A;
    // text-align: right;
    font-family: Nunito;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 33.6px; /* 186.667% */
    margin-top: 65px;
}
  