.back-reporting-button{
    text-align: end;

    button{
        border-radius: 33px;
        background: #3BC97B;
        border: none;
        color: white;
        align-items: center;
        padding: 10px 25px;
        cursor:pointer

    }
    button:hover{
        opacity: 0.8
    }
    

}

.reporting-detail-header{
    display:flex;
    justify-content: space-between;
}

.detail-filter-container{
    display:flex; 
    gap: 25px;
    justify-content: end;
    align-items: center;
    margin-top:15px;

    input{
        border-radius: 50px;
        border: 1px solid var(--grey-grey-30, #EAEEF4);
        background: var(--primary-white, #FFF);
        padding: 11px 25px 11px 25px;
        display: flex;
        width: 235px;
        justify-content: space-between;

    }
    input:focus{
        outline:none;
    }
}

.reporting-row{
    cursor: pointer;
}

.reporting-row:hover{
    opacity:0.8
}