.compliance-filter-container-modal{
    position: "fixed";
    transform: "translate(116px, 96px)";
    // zIndex: 9999;
    display: flex;
    // align-items: center;
    padding-top: 250px;
    justify-content: center;
  
    // padding: 45px;
    .modal-title{
        color: #3A3A3A;
        font-family: Nunito;
        font-size: 23px;
        font-style: normal;
        font-weight: 800;
  }
  
  .centered-content {
    background-color: white;
    // padding: 20px;
    border-radius: 8px;
    height:50%;
    // overflow-y: auto;
    // width: 60%;
    width: 30%;
    padding: 20px;
    position: relative;
  
  }

  .high-crime-cities{
    input{
        width: 50%;
        border-radius: 5px;
        border: 1px solid #D7D7D7;
    }

    input:focus{
        outline:none;
    }
    .filter-submit{
        position: absolute;
        bottom: 0;
        right:0;
    }
    .submit-button{

        border-radius: 8px;
        background: #3BC97B;
        border: none;
        display: flex;
        color: white;
        align-items: center;
        padding: 10px 25px;
        margin-bottom:10px;
        margin-right:10px;
    }

    .city-list {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        margin-top: 10px;
        overflow-y: auto;

      }
    
      .city-item {
        background-color: #f2f2f2;
        border-radius: 5px;
        padding: 5px 10px;
        display: flex;
        align-items: center;
        height:34px;
    
        .remove-city {
          cursor: pointer;
          margin-left: 5px;
        }
    }


  }

}