.post-entry-container{
    position: "fixed";
    transform: "translate(116px, 96px)";
    display: flex;
    padding-top: 250px;
    justify-content: center;
    

    .post-entry-form-container{
        margin-top: 25px;
    }

    .post-entry-account-containers{
        margin-top:15px;
    }

    input{
        border-radius: 4px;
        border: 1px solid #D6E1E6;
        padding: 8px 5px;
        width: 100%;
    }

    .currency-box{
        border-radius: 4px;
        border: 1px solid #D6E1E6;
        padding: 8px 5px;

    }

    .post-entry-confirm {
        border-radius: 33px;
        background: #145388;
        padding-top: 8px;
        padding-bottom: 8px;
        color: #FFF;
        font-family: Nunito;
        font-size: 15px;
        font-style: normal;
        font-weight: 600;
        line-height: 26.4px;
        display: flex;
        justify-content: space-evenly;
        // width: 30%;
        width: 173px;
        cursor: pointer;
    }

    input:focus{
        outline: none;
    }

    select:focus{
        outline: none;
    }

    select{
        border-radius: 4px;
        border: 1px solid #D6E1E6;
        padding: 8px 5px;
        width: 100%;
    }

    .modal-title{
        font-family: Nunito;
        color: #3A3A3A;
        font-size: 17px;
        font-style: normal;
        font-weight: 700;
        line-height: 26.4px;
        padding-top: 20px;
  }

  .centered-content {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    // min-height:50%;

    max-height:60%;
    // width: 60%;
    min-width: 40%;
  }

  .customer-porfile-container-modal{
    background: white;
    // padding: 15px 40px 24px 40px;
    border-radius: 12px;
    background: #FFF;
    // box-shadow: 0px 1px 15px 0px rgba(0, 0, 0, 0.04);
    display:flex;
  }
}
  