.reporting-table-container{
    margin-top: 54px;
    .reporting-tab-headers{
        color: #4C5566;
        // text-align: center;
        font-family: Nunito;
        font-size: 15px;
        font-style: normal;
        font-weight: 800;
        line-height: 33.6px; /* 224% */
    }

    .type-background{
        background: #F5F5F5;
    }
    .download-background{
        background: #D6E1E6;
    }
    .empty-background{
        background: rgba(242, 242, 242, 0.39);
    }

    .reporting-download-container{
        margin-top: 61px;
    }

    .download-description{
        color: #4C5566;
        font-family: Nunito;
        font-size: 15px;
        font-style: normal;
        font-weight: 700;
        line-height: 33.6px; /* 224% */
    }

    .reporting-download-line{
        padding-top: 18px;
        padding-bottom: 18px;
        border-bottom: 1px solid #E6E6E6;
    }

    .download-type{
        cursor: pointer;
    }
    .download-type:hover{
        opacity:0.8
    }

    .download-csv{
        color: #3BC97B;
        font-family: Nunito;
        font-size: 15px;
        font-style: normal;
        font-weight: 700;
        line-height: 33.6px; /* 224% */
        text-decoration-line: underline;
    }

    .download-xls{
        color: #6FB327;
        font-family: Nunito;
        font-size: 15px;
        font-style: normal;
        font-weight: 700;
        line-height: 33.6px; /* 224% */
        text-decoration-line: underline;
    }
    .download-pdf{
        color: #BA0012;
        font-family: Nunito;
        font-size: 15px;
        font-style: normal;
        font-weight: 700;
        line-height: 33.6px; /* 224% */
        text-decoration-line: underline;
    }
}
