.page-container{

    width:100%;
    overflow-y: auto;
    background-color: #F8F8F8;
    padding: 44px 61px 44px 61px; 

}

.page-title{
    font-size: 27px;
    font-style: normal;
    font-weight: 400;
    line-height: 33.6px;
    padding-bottom: 21px;
    border-bottom: 1px solid #D7D7D7;
}


.w100{
    width: 100%;
}

p{
    margin-bottom: 0;
}

.pointer{
    cursor: pointer;
}

.pointer:hover{
    opacity:0.8;
}

.excel-icon{
    height: 25px;
}

