.audit-container{
    overflow: auto;
    .audit-header {
        display: flex;
        justify-content: space-between;
    }
    .audit-title{
        color: #3A3A3A;
        font-family: Nunito;
        font-size: 16px;
        font-style: normal;
        font-weight: 800;
        line-height: 33.6px;
    }

    .audit-rule-container{
        gap: 50px;
        align-items: center;
        padding-top:5px;
        padding-bottom:5px;
        border-bottom: 1px solid #efefef;
        justify-content: space-between;
    }

    .audit-text {
        word-wrap: break-word; 
        font-size:14px;

        span{
            font-weight: 700;
        }
      }

      .audit-checkbox{
        height:27px;
        width:27px;
      }
    
}