.liquidity-tab-header{
    color: #3A3A3A;
    font-family: Nunito;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 33.6px; /* 168% */
}

.header-container-liquidity{
    margin-top:50px;

}

.add-liquidity-button{
    border-radius: 33px;
    background: #3BC97B;
    border: none;
    color: #FFF;
    text-align: right;
    font-family: Nunito;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: 26.4px; /* 176% */
    padding: 8px 20px;
    cursor: pointer;
}

.add-liquidity-button:hover{
    opacity:0.8;
}