.user-accounts-tab{
    background:"red";
    color:"red";

    .add-funds-crm-button{
        border-radius: 33px;
        background: #3BC97B;
        border: none;
        display: flex;
        color: white;
        align-items: center;
        padding: 5px 10px;
        width:30%;
        justify-content: center;
    }

    .accounts-tab-header{
        font-family: Nunito;
        // background-color: #f8f8f8;
        color: #3A3A3A;
        font-size: 15px;
        font-style: normal;
        font-weight: 700;
        line-height: 26.4px;
        padding-top: 15px;
    }

    .balance-container{
        margin: auto;
        width: 80%;
        // display:flex;
        // justify-content: center;
        // align-items: center;
    }

    .accounts-information{
        margin-top: 60px;
    }

    .account-details{
        color: #7E92A2;
        font-family: Nunito;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 30px; /* 187.5% */
        border-bottom: 1px solid #EAEEF4;
        padding-bottom: 13px;
        padding-top: 12px;
        display:flex;

        img{
            padding-right: 9px;
            // flex:2
        }
    }

    .account-info-type{
        flex: 4

    }

    .account-info-response{
        flex: 5;
        color: #00365A;
        font-family: Nunito;
        font-size: 16px;
        font-style: normal;
        font-weight: 600;
        line-height: 30px; /* 187.5% */

    }


}

.KYC-ID-box{
    margin-top: 5px;
    margin-bottom: 5px;
    color: #6B7280;

}

